}

#autocomplete-wrapper input:focus{
    border-radius: 10px 10px 0 0;
}

#autocomplete-wrapper > div{
    width: 100%;
}

#autocomplete-wrapper > div > div div:last-child{
    border-radius: 0 0 10px 10px;
}

